<template>
  <div class="Cmain">
    <div class="contents">
      <div class="head">
        <a-dropdown>
          <a-menu slot="overlay" @click="handleMenuClick">
            <a-menu-item key="1">
              <a-icon type="user" />1st menu item
            </a-menu-item>
            <a-menu-item key="2">
              <a-icon type="user" />2nd menu item
            </a-menu-item>
            <a-menu-item key="3"> <a-icon type="user" />3rd item </a-menu-item>
          </a-menu>
          <a-button style="margin-left: 8px">
            角色状态 <a-icon type="down" />
          </a-button>
        </a-dropdown>
        <a-input-search
          placeholder="请输入想要搜索的名称"
          style="width: 500px; margin-left: 10px"
          v-model="Searchs"
          @input="onSearch(Searchs)"
        />
        <a-button type="primary" @click="addshow">
          <a-icon type="plus" />添加系统角色
        </a-button>
      </div>
      <div class="tianjia" v-show="addshows">
        <div>
          <h2>{{ caozuo }}系统角色</h2>
          <a-button type="link" @click="addshow">
            <a-icon type="close" />
          </a-button>
        </div>
        <div>
          角色名称： <a-input placeholder="请输入名称" v-model="name" />
          <br />
          <br />
          描述:
          <a-textarea placeholder="请输入该描述" auto-size v-model="desc" />
          <br />
          <br />
          状态:&emsp;<a-radio-group
            name="radioGroup"
            :default-value="1"
            v-model="status"
          >
            <a-radio :value="1"> 启用 </a-radio>
            <a-radio :value="0"> 停用 </a-radio>
          </a-radio-group>
          <br />
          <br />
          <div>
            <span>管理权限:</span>
            <div>
              <a-checkbox-group @change="onChange">
                <a-row>
                  <a-col :span="12">
                    <a-checkbox value="1"> 主页 </a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="2"> 企业管理 </a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="3"> 角色管理 </a-checkbox>
                  </a-col>
                </a-row>
              </a-checkbox-group>
            </div>
          </div>
          <br />
          <a-button type="primary" @click="listadd" block> 提交 </a-button>
          <span style="display: block; color: red; margin: auto">{{
            error
          }}</span>
        </div>
      </div>
      <div class="center">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column label="姓名" width="180">
            <template slot-scope="scope">
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.rolename }}</el-tag>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="描述" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.describe }}</span>
            </template>
          </el-table-column>
          <el-table-column label="角色权限配置" width="220">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{
                converquanxian(scope.row.powers)
              }}</span>
            </template>
          </el-table-column>
          <el-table-column label="角色启用状态" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">
                <i :class="scope.row.status == 1 ? 'i_greey' : 'i_red'"></i
                >{{ converstatus(scope.row.status) }}</span
              >
            </template>
          </el-table-column>
          <el-table-column label="最后编辑时间" width="180">
            <template slot-scope="scope">
              <i class="el-icon-time">{{
                formatTimestamp(scope.row.lasttime)
              }}</i>
              <span style="margin-left: 10px"></span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                @click="handleEdit(scope.row)"
                circle
              ></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="handleDelete(scope.row)"
                circle
              ></el-button>
              <el-button
                ref="button"
                :type="scope.row.status==1 ? 'danger':'info' "
                :icon="
                  scope.row.status == 1
                    ? 'el-icon-switch-button'
                    : 'el-icon-video-play'
                "
                @click="start(scope.row)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="footer">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :current-page.sync="pagecount"
          @current-change="pagefy"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getList,
  Listadd,
  Listupdate,
  Delete,
  Status,
  Search,
} from "../api/Character";
export default {
  data() {
    return {
      id: "",
      caozuo: "",
      tableData: [],
      addshows: false,
      name: "",
      desc: "",
      status: "",
      powers: "",
      error: "",
      total: 0,// 总记录数
      page: 0,// 当前页码
      pagecount: 1, // 总页数
      Searchs: "",
    };
  },
  created() {
    this.Listshow();
  },
  methods: {
    //分页
    pagefy() {
      this.Listshow(this.pagecount);
    },
    //添加
    //列标添加项
    listadd() {
      if (this.id == null || this.id == "") {
        Listadd({
          rolename: this.name,
          describe: this.desc,
          powers: this.powers,
          status: this.status,
        }).then((res) => {
          console.log(res);
          if (res.code == 0) {
            this.Listshow();
            this.addshows = false;
            //清空
            this.name = "";
            this.desc = "";
            this.status = "";
            this.powers = "";
          } else {
            this.error = res.msg;
          }
        });
      } else {
        Listupdate({
          id: this.id,
          rolename: this.name,
          describe: this.desc,
          powers: this.powers,
          status: this.status,
        }).then((res) => {
          console.log(res);

          if (res.code == 0) {
            this.Listshow();
            this.addshows = false;
            //清空
            this.name = "";
            this.desc = "";
            this.status = "";
            this.powers = "";
          } else {
            this.error = res.msg;
          }
        });
      }
    },
    //盒子显示
    addshow() {
      this.caozuo = "添加";
      if (this.addshows == true) {
        this.addshows = false;
        this.name = "";
        this.desc = "";
        this.status = "";
        this.powers = "";
      } else if (this.addshows == false) {
        this.addshows = true;
      }
    },
    //多选状态
    onChange(checkedValues) {
      this.powers = checkedValues.toString();
      console.log(this.powers);
    },
    //时间戳转换
    formatTimestamp(timestampInSeconds) {
      // 确保时间戳是以毫秒为单位
      const timestampInMilliseconds = timestampInSeconds * 1000;
      const date = new Date(timestampInMilliseconds);
      const formattedTime = `${date.getFullYear()}-${
        date.getMonth() + 1
      }-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
      return formattedTime;
    },
    //表格列表回显
    Listshow(page) {
      getList(page).then((res) => {
        console.log(res);
        this.tableData = res.data.data;
        this.total = res.data.pagecount;// 更新总数
      });
    },
    //表格操作
    //修改
    handleEdit(row) {
      console.log(row);
      this.id = row.id;
      this.caozuo = "修改";
      this.name = row.rolename;
      this.desc = row.describe;
      this.status = row.status;
      this.powers = row.powers;
      if (this.addshows == true) {
        this.addshows = false;
        //清空
        this.name = "";
        this.desc = "";
        this.status = "";
        this.powers = "";
        this.error = "";
      } else if (this.addshows == false) {
        this.addshows = true;
      }
    },
    //删除
    handleDelete(row) {
      this.$confirm({
        title: "确定要删除这项任务吗？",
        content: "这将永久删除该任务。",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk: () => {
          Delete(row.id).then((res) => {
            console.log(res);
            this.Listshow();
          });
          console.log("OK");
        },
        onCancel() {
          console.log("Cancel");
        },
      });
    },
    //状态
    start(row) {
      if (row.status == 1) {
        this.$confirm({
          title: "确定要停用这项任务吗？",
          content: "这将停用该任务。",
          okText: "确定",
          okType: "danger",
          cancelText: "取消",
          onOk: () => {
            Status(row.id, 0).then((res) => {
              console.log(res, "停用");
              this.Listshow();
            });
            console.log("OK");
          },
          onCancel() {
            console.log("Cancel");
          },
        });
      } else if (row.status == 0) {
        this.$confirm({
          title: "确定要启用这项任务吗？",
          content: "这将启用该任务。",
          okText: "确定",
          okType: "danger",
          cancelText: "取消",
          onOk: () => {
            Status(row.id, 1).then((res) => {
              console.log(res, "启用");
              this.Listshow();
            });
            console.log("OK");
          },
          onCancel() {
            console.log("Cancel");
          },
        });
      }
    },
    //状态改变
    converstatus(sta) {
      return sta === 0 ? "停用" : "启用";
    },
    //权限管理
    converquanxian(quanxian) {
      const numbers = quanxian.split(","); // 分割字符串为数组
      const permissions = numbers
        .map((num) => {
          switch (
            num.trim() // 去除前后空格并根据数字返回权限
          ) {
            case "1":
              return "主页";
            case "2":
              return "企业管理";
            case "3":
              return "角色管理";
            default:
              return ""; // 或者可以处理为 undefined 或者其他错误提示
          }
        })
        .filter((permission) => permission); // 过滤掉无效的转换结果

      return permissions.length ? permissions.join("，") : "无效的权限标识"; // 合并权限名称或返回错误提示
    },
    //下拉框
    handleButtonClick(e) {
      console.log("click left button", e);
    },
    handleMenuClick(e) {
      console.log("click", e);
    },
    //搜索
    onSearch(value) {
      console.log(value);
      Search(value).then((res) => {
        console.log(res);
        this.tableData = res.data.data;
        this.total = res.data.pagecount;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.i_greey {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin-right: 5px;
  background: #38d88f;
}
.i_red {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin-right: 5px;
  background: red;
}
.tianjia {
  position: absolute;
  top: 150px;
  left: 500px;
  width: 500px;
  height: 400px;
  background: white;
  border: 1px solid rgb(244, 244, 244);
  //盒子阴影
  box-shadow: 0 0 10px rgb(223, 223, 223);
  z-index: 10;
  padding: 10px;
  > div:first-child {
    height: 40px;
    border-bottom: 1px solid rgb(244, 244, 244);
    position: relative;
    margin-bottom: 10px;
    > button {
      position: absolute;
      top: 0px;
      right: 5px;
    }
  }
}
.Cmain {
  width: 100%;
  // height: 100vh;
  background: rgb(231, 231, 231);
  padding: 10px;
}
.contents {
  width: 100%;
  background: rgb(255, 255, 255);
  padding: 10px;
  .head {
    button:last-child {
      float: right;
    }
  }
  .center {
    height: 700px;
    margin-bottom: 10px;
  }
}
</style>
