import { apiGet } from "./api";
//列表获取
export function getList(page) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/role/getroleslist", { page: page }).then((res) => {
      resolve(res);
    });
  });
}
//列表添加
export function Listadd(str) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/role/addroles", str).then((res) => {
      resolve(res);
    });
  });
}
//列表修改
export function Listupdate(str) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/role/uproles", str).then((res) => {
      resolve(res);
    });
  });
}
//删除
export function Delete(id) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/role/delroles?id=" + id).then((res) => {
      resolve(res);
    });
  });
}
//列表状态

export function Status(id, str) {
  return new Promise((resolve) => {
    apiGet(`/index.php/index/role/stoproles?id=${id}&&status=${str}`).then(
      (res) => {
        resolve(res);
      }
    );
  });
}

export function Search(name) {
  return new Promise((resolve) => {
    apiGet(`/index.php/index/role/getsearchroles?rolename=${name}`).then(
      (res) => {
        resolve(res);
      }
    );
  });
}
